:root {
  --search-icon-big: url("Search.ccd3c07b.svg") 26px 50% no-repeat;
  --search-icon-small: url("Search.ccd3c07b.svg") 13px 50% no-repeat;
  --font-color: #3d4e61;
  --linear-gradient-focus: linear-gradient(180deg, #fff, #f6f6f7);
  --border-input: 1px solid #e1e7ed;
  --shadow-input: 0 1px 8px 0 #3d4e611a;
  --shadow-input-hovered: 0 3px 3px 0 #3d4e611a;
}

body {
  font-family: Avenir, sans-serif;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 800;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

.search-bar {
  margin-top: 20px;
}

.search-bar__input {
  box-sizing: border-box;
  border: var(--border-input);
  box-shadow: var(--shadow-input);
  color: var(--font-color);
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
}

.big {
  background: var(--search-icon-big);
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.small {
  background: var(--search-icon-small);
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-bar__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.big:hover {
  background: var(--search-icon-big), linear-gradient(180deg, #fff, #f6f6f7);
}

.small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input::placeholder {
  color: var(--font-color);
  font-family: inherit;
  font-weight: 300;
}

.small::placeholder {
  font-size: 14px;
}

.search-bar__input:focus {
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

.big:focus {
  background: var(--search-icon-big), linear-gradient(180deg, #fff, #f6f6f7);
}

.small:focus {
  color: var(--font-color);
  background: var(--search-icon-small), linear-gradient(180deg, #fff, #f6f6f7);
  background-size: 11px;
}
